import { createRouter, createWebHistory } from "vue-router";
import HomePage from "../components/Trang-Chu.vue";
import MovieList from "../components/Danh-Sach-Phim.vue";
import MovieDetail from "../components/Chi-Tiet-Phim.vue";
import MoviePlayer from "../components/Xem-Phim.vue";

const routes = [
  { path: "/", component: HomePage }, // Set HomePage as the default route
  {
    path: "/movies",
    component: MovieList,
    props: (route) => ({ query: route.query }),
  },
  {
    path: "/movie/:slug",
    name: "MovieDetail",
    component: MovieDetail,
    props: true,
  },
  {
    path: "/xem-phim/:slug",
    name: "MoviePlayer",
    component: MoviePlayer,
    props: true,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
