<template>
  <div class="movie-detail-page">
    <MovieNavbar
      :genres="genres"
      :countries="countries"
      :showGenreDropdown="showGenreDropdown"
      :showCountryDropdown="showCountryDropdown"
      @toggle-genre-dropdown="toggleGenreDropdown"
      @toggle-country-dropdown="toggleCountryDropdown"
      @load-movies-by-genre="loadMoviesByGenre"
      @load-movies-by-country="loadMoviesByCountry"
    />
    <div v-if="isLoading" class="loading-spinner">
      <span>Đang tải...</span>
      <div class="spinner"></div>
    </div>
    <div v-else class="container">
      <div class="left-section">
        <div
          class="movie-detail-container"
          :style="{ backgroundImage: `url(${movie.thumb_url})` }"
        >
          <div class="overlay"></div>
          <div class="movie-content">
            <div class="movie-thumbnail">
              <img :src="movie.poster_url" alt="Thumbnail" loading="lazy" />
              <router-link
                :to="{
                  name: 'MoviePlayer',
                  params: { slug: movie.slug, episodeSlug: 'tap-01' },
                }"
                class="play-button"
              >
                <i class="fas fa-play"></i>
              </router-link>
            </div>
            <div class="movie-info">
              <h1>{{ movie.name }}</h1>
              <p>
                <i class="fas fa-film"></i>
                <strong>Tên khác:</strong>
                {{ movie.origin_name || "Đang cập nhật" }}
              </p>
              <p>
                <i class="fas fa-info-circle"></i>
                <strong>Trạng thái:</strong>
                {{ translatedStatus }}
              </p>
              <p>
                <i class="fas fa-list-ol"></i>
                <strong>Số tập:</strong>
                {{ movie.episode_total || "Đang cập nhật" }}
              </p>
              <p>
                <i class="fas fa-clock"></i>
                <strong>Thời lượng:</strong> {{ movie.time || "Đang cập nhật" }}
              </p>
              <p>
                <i class="fas fa-calendar-alt"></i>
                <strong>Năm phát hành:</strong>
                {{ movie.year || "Đang cập nhật" }}
              </p>
              <p>
                <i class="fas fa-video"></i>
                <strong>Chất lượng:</strong>
                {{ movie.quality || "Đang cập nhật" }}
              </p>
              <p>
                <i class="fas fa-language"></i>
                <strong>Ngôn ngữ:</strong> {{ movie.lang || "Đang cập nhật" }}
              </p>
              <p>
                <i class="fas fa-tags"></i>
                <strong>Thể loại:</strong>
                <span
                  v-for="category in movie.category"
                  :key="category.id"
                  class="category-tag"
                  @click="
                    $router.push({
                      path: '/movies',
                      query: {
                        filter: `https://phimapi.com/v1/api/the-loai/${category.slug}?page=1`,
                      },
                    })
                  "
                >
                  {{ category.name }}
                </span>
              </p>
              <p>
                <i class="fas fa-globe"></i>
                <strong>Quốc gia:</strong>
                <span
                  v-for="country in movie.country"
                  :key="country.id"
                  class="country-tag"
                  @click="
                    $router.push({
                      path: '/movies',
                      query: {
                        filter: `https://phimapi.com/v1/api/quoc-gia/${country.slug}?page=1`,
                      },
                    })
                  "
                >
                  {{ country.name }}
                </span>
              </p>
              <div class="rating-info">
                <div class="rating-container">
                  <div class="rating-percentage">
                    <div
                      class="circle"
                      :style="{
                        background: `conic-gradient(#ff6347 ${circlePercentage}%, #fff ${circlePercentage}%)`,
                      }"
                    >
                      <span class="percentage-text">
                        {{ circlePercentage }}%
                      </span>
                    </div>
                  </div>
                  <div class="stars-container">
                    <div class="stars">
                      <i
                        v-for="n in 10"
                        :key="n"
                        :class="
                          n <= Math.round(movie.vote_average)
                            ? 'fas fa-star'
                            : 'far fa-star'
                        "
                      ></i>
                    </div>
                    <p class="rating-text">
                      ({{ movie.vote_average || "0" }}/10 từ
                      {{ movie.vote_count || "0" }} lượt vote)
                    </p>
                  </div>
                </div>
                <div class="additional-info">
                  <span>
                    <i class="fas fa-list-ol"></i>
                    {{
                      movie.status && movie.status.toLowerCase() === "completed"
                        ? movie.episode_current
                        : `${movie.episode_current || "N/A"}/${
                            movie.episode_total || "N/A"
                          }`
                    }}
                  </span>
                  <span>
                    <i class="fas fa-calendar-alt"></i>
                    {{ movie.year || "N/A" }}
                  </span>
                  <span>
                    <i class="fas fa-eye"></i> {{ movie.view || 0 }} Lượt Xem
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="movie-description-tabs">
          <div class="tabs">
            <button
              class="tab-button"
              :class="{ active: activeTab === 'description' }"
              @click="activeTab = 'description'"
            >
              <i class="fas fa-info-circle"></i> Giới thiệu
            </button>
            <button
              class="tab-button"
              :class="{ active: activeTab === 'trailer' }"
              @click="activeTab = 'trailer'"
            >
              <i class="fas fa-video"></i> Trailer
            </button>
            <button
              class="tab-button"
              :class="{ active: activeTab === 'characters' }"
              @click="activeTab = 'characters'"
            >
              <i class="fas fa-users"></i> Diễn viên
            </button>
            <button
              class="tab-button"
              :class="{ active: activeTab === 'directors' }"
              @click="activeTab = 'directors'"
            >
              <i class="fas fa-user-tie"></i> Đạo diễn
            </button>
          </div>
          <div class="tab-content">
            <div v-if="activeTab === 'description'" class="description-tab">
              <h3>Giới thiệu</h3>
              <p>{{ movie.content || "Đang cập nhật" }}</p>
            </div>
            <div v-if="activeTab === 'trailer'" class="trailer-tab">
              <h3>Trailer</h3>
              <iframe
                v-if="embeddedTrailerUrl"
                :src="embeddedTrailerUrl"
                frameborder="0"
                allowfullscreen
                class="trailer-iframe"
                loading="lazy"
              ></iframe>
              <p v-else>Trailer hiện chưa có sẵn.</p>
            </div>
            <div v-if="activeTab === 'characters'" class="characters-tab">
              <h3>Nhân vật</h3>
              <p>
                <span
                  v-if="movie.actor && movie.actor.length > 0"
                  class="actor-list"
                >
                  {{ movie.actor.join(", ") }}
                </span>
                <span v-else>Đang cập nhật</span>
              </p>
            </div>
            <div v-if="activeTab === 'directors'" class="directors-tab">
              <h3>Đạo diễn</h3>
              <p>
                <span
                  v-if="movie.director && movie.director.length > 0"
                  class="director-list"
                >
                  {{ movie.director.join(", ") }}
                </span>
                <span v-else>Đang cập nhật</span>
              </p>
            </div>
          </div>
        </div>
        <div class="episode-list" v-if="episodes.length > 0">
          <div class="server" v-for="(server, index) in episodes" :key="index">
            <h4>{{ server.server_name }}</h4>
            <div class="episode-buttons">
              <button
                v-for="episode in server.server_data"
                :key="episode.slug"
                class="episode-button"
                @click="handleEpisodeClick(episode.slug)"
              >
                {{ episode.name }}
              </button>
            </div>
          </div>
        </div>
      </div>
      <RightSection
        :newMovies="newMovies"
        @redirect="redirectToMovieListWithoutApi"
      />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import RightSection from "./Thanh-Ben.vue";

export default {
  components: {
    RightSection,
  },
  data() {
    return {
      movie: {},
      episodes: [],
      isLoading: true,
      genres: [],
      countries: [],
      showGenreDropdown: false,
      showCountryDropdown: false,
      newMovies: [],
      activeTab: "description",
    };
  },
  mounted() {
    this.scrollToTop();
    this.fetchMovieDetails();
    this.fetchGenres();
    this.fetchCountries();
    this.fetchNewMovies();
  },
  watch: {
    $route: {
      immediate: true,
      handler() {
        this.scrollToTop();
        this.fetchMovieDetails();
      },
    },
  },
  computed: {
    embeddedTrailerUrl() {
      if (!this.movie.trailer_url) return null;
      try {
        const url = new URL(this.movie.trailer_url);
        if (
          url.hostname.includes("youtube.com") ||
          url.hostname.includes("youtu.be")
        ) {
          const videoId =
            url.searchParams.get("v") || url.pathname.split("/").pop();
          return `https://www.youtube.com/embed/${videoId}`;
        }
        return this.movie.trailer_url;
      } catch {
        return null;
      }
    },
    circleBorderColor() {
      const percentage = this.movie.vote_average * 10;
      if (percentage >= 75) {
        return "#4caf50";
      } else if (percentage >= 50) {
        return "#ffeb3b";
      } else {
        return "#f44336";
      }
    },
    circlePercentage() {
      const voteAverage = parseFloat(this.movie.vote_average) || 0;
      return (voteAverage * 10).toFixed(0);
    },
    translatedStatus() {
      const statusMap = {
        ongoing: "Đang chiếu",
        completed: "Hoàn thành",
      };
      return statusMap[this.movie.status?.toLowerCase()] || "Đang cập nhật";
    },
  },
  methods: {
    scrollToTop() {
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
    async fetchMovieDetails() {
      const slug = this.$route.params.slug;
      try {
        const response = await axios.get(`https://phimapi.com/phim/${slug}`);
        if (response.data && response.data.movie) {
          this.movie = {
            ...response.data.movie,
            vote_average: response.data.movie.tmdb?.vote_average || 0,
            vote_count: response.data.movie.tmdb?.vote_count || 0,
          };
          this.episodes = response.data.episodes.map((server) => ({
            ...server,
            server_data: server.server_data.map((episode) => ({
              ...episode,
              slug: episode.slug || episode.filename,
            })),
          }));
        } else {
          console.error("Dữ liệu không hợp lệ:", response.data);
        }
      } catch (error) {
        console.error("Có lỗi xảy ra khi lấy dữ liệu phim:", error);
      } finally {
        this.isLoading = false;
      }
    },
    async fetchGenres() {
      try {
        const response = await axios.get("https://phimapi.com/the-loai");
        this.genres = response.data || [];
      } catch (error) {
        console.error("Lỗi khi lấy danh sách thể loại:", error);
      }
    },
    async fetchCountries() {
      try {
        const response = await axios.get("https://phimapi.com/quoc-gia");
        this.countries = response.data || [];
      } catch (error) {
        console.error("Lỗi khi lấy danh sách quốc gia:", error);
      }
    },
    async fetchNewMovies() {
      try {
        const response = await axios.get(
          "https://phimapi.com/danh-sach/phim-moi-cap-nhat-v3?page=1"
        );
        this.newMovies = response.data?.items || [];
      } catch (error) {
        console.error("Lỗi khi lấy danh sách phim mới:", error);
      }
    },
    playEpisode(slug, linkEmbed) {
      if (linkEmbed) {
        this.$router.push({
          name: "MoviePlayer",
          params: { slug: this.movie.slug, episodeSlug: slug },
        });
      } else {
        alert("Không thể phát tập này.");
      }
    },
    redirectToMovieListWithoutApi() {
      this.$router.push({ path: "/movies" });
    },
    handleEpisodeClick(episodeSlug) {
      this.$router.push({
        name: "MoviePlayer",
        params: { slug: this.movie.slug },
        query: { episodeSlug },
      });
    },
  },
};
</script>

<style scoped>
.movie-detail-page {
  background-color: #1c1c1c;
  color: white;
  min-height: 100vh;
  padding: 15px;
  width: 100%;
  box-sizing: border-box;
}

.container {
  display: flex;
  gap: 15px;
  padding-top: 60px;
  max-width: 1450px;
  margin: 0 auto;
  flex-direction: row;
  width: 100%;
  box-sizing: border-box;
}

.left-section {
  flex: 2;
}

.right-section {
  flex: 1;
}

