  position: relative;
  max-width: 100%;
  box-sizing: border-box;
  overflow: hidden;
}

.movie-detail-container .overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  border-radius: 8px;
  z-index: 1;
}

.movie-content {
  display: flex;
  align-items: flex-start;
  gap: 15px;
  position: relative;
  z-index: 2;
  flex-direction: row;
  width: 100%;
  box-sizing: border-box;
}

.movie-thumbnail {
  position: relative;
  flex-shrink: 0;
}

.movie-thumbnail img {
  width: 250px;
