  flex-wrap: wrap;
}

.movie-info i {
  color: #ff6347;
  font-size: 14px;
}

.actor-list {
  white-space: normal;
  word-break: break-word;
}

.category-tag,
.country-tag {
  display: inline-block;
  background-color: #ff6347;
  color: white;
  padding: 4px 8px;
  border-radius: 12px;
